import base from "./airtable-base";
import { Component } from "react";
import Klok from "./Klok";
import { Link } from "react-router-dom";

export default class TestYourselfNow extends Component {
  state = {};

  componentDidMount() {
    this.setState({
      startTime: Date.now()
    });

    window.addEventListener("keydown", this.boundHandleSpacePressed);
  }

  componentWillUnmount() {
    window.removeEventListener("keydown", this.boundHandleSpacePressed);
  }

  boundHandleSpacePressed = this.handleSpacePressed.bind(this);

  handleSpacePressed() {
    let durationInMillis = Date.now() - this.state.startTime;

    base("Self-test").create(
      [
        {
          fields: {
            Name: this.props.name,
            Duration: durationInMillis / 1000,
            "Uploaded On": new Date(),
            Color: "rgba(50,255,100,0.80)"
          }
        }
      ],
      (err, records) => {
        if (err) {
          console.error(err);
          return;
        } else {
          window.location.assign("/self-test");
        }
      }
    );
  }

  render() {
    return (
      <div className="App">
        <h1>
          Press space or click on the arrow when you think 7 minutes have passed
        </h1>
        <img
          src="./arrow.png"
          onClick={this.handleSpacePressed.bind(this)}
          className="arrow"
        />

        {/* {this.props.name} */}
      </div>
    );
  }
}
