import base from "./airtable-base";
import { Component } from "react";
import Klok from "./Klok";
import { Link } from "react-router-dom";

export default class Selftest extends Component {
  state = {
    klokjes: {}
  };

  componentDidMount() {
    base("Self-test")
      .select({
        view: "Grid view"
      })
      .eachPage((records, fetchNextPage) => {
        console.log(records);
        const klokjes = { ...this.state.klokjes };

        for (const record of records) {
          klokjes[record.id] = record.fields;
        }

        this.setState({
          klokjes
        });
      });
  }

  render() {
    return (
      <div className="App">
        <h1>Hall of Sevens</h1>

        <p>How long do you think seven minutes takes? Do the self-test.</p>

        <p>
          <input
            name={this.state.name}
            placeholder="Enter your name"
            onInput={(e) => this.props.setName(e.target.value)}
          />
          <span> to join the Hall of Sevens,</span>
        </p>

        <p>
          {" "}
          <span> then </span>
          <Link className="button" to="/test-yourself-now">
            Press this button
          </Link>{" "}
          to start.
        </p>

        <div className="klokjesContainer">
          {Object.entries(this.state.klokjes).map(([id, klok]) => (
            <Klok
              key={id}
              name={klok["Name"]}
              duration={klok["Duration"]}
              color={klok["Color"]}
            />
          ))}
        </div>
      </div>
    );
  }
}
