import base from "./airtable-base";
import { Component } from "react";
import Klok from "./Klok";

export default class Me extends Component {
  state = {
    klokjes: {}
  };

  componentDidMount() {
    base("Me")
      .select({
        view: "Grid view"
      })
      .eachPage((records, fetchNextPage) => {
        console.log(records);
        const klokjes = { ...this.state.klokjes };

        for (const record of records) {
          klokjes[record.id] = record.fields;
        }

        this.setState({
          klokjes
        });
      });
  }

  render() {
    return (
      <div className="App">
        <h1>SENSEyourSEVEN</h1>
        <h2>Me</h2>
        <div className="klokjesContainer">
          {Object.entries(this.state.klokjes).map(([id, klok]) => (
            <Klok
              key={id}
              name={klok["Name"]}
              duration={klok["Duration"]}
              color={klok["Color"]}
            />
          ))}
        </div>
      </div>
    );
  }
}
